<template lang="pug">
  .section--babysitter-custom-filters

    //- KIND

    h3.h4 Kind of babysitting
    tag-check-box-group(
      kind="kind"
      v-model="form.kind"
    )


    //- EXPERIENCE

    h3.h4 Experience
    tag-check-box-group(
      kind="experience"
      v-model="form.experience"
    )

    //- AGE RANGES

    h3.h4 Age of your kids
    tag-check-box-group(
      kind="age_ranges"
      v-model="form.age_ranges"
    )


    //- QUALIFICATIONS

    h3.h4 Qualification
    tag-check-box-group(
      kind="qualification"
      v-model="form.qualification"
    )

    //- LANGUAGES

    h3.h4 Languages
    language-selector(
      @select="addLanguage"
      :show-choose="true"
    )

    b-button.mr-2.is-flex.valign(@click="removeLanguage(lang)" v-for="lang in form.languages" variant="white")
      i.flag-icon.mr-2(:class="'flag-icon-'+lang.substring(3).toLowerCase()")
      i.mdi.text-grey remove_circle_outline


</template>

<script>
  import {mapGetters, mapActions} from "vuex";
  import {tagsOptions} from "@/config";
  import TagCheckBoxGroup from "../ui/tagCheckBoxGroup";
  import LanguageSelector from "../ui/language-selector";
  import debounce from 'lodash/debounce';

  export default {
    name: "BabysitterCustomFilters",
    components: {LanguageSelector, TagCheckBoxGroup},
    props: [],
    data() {
      return {
        // UX
        isLoading: false,
        form: {
          kind: [],
          experience: [],
          qualification: [],
          languages: []
        },
        ...tagsOptions
      };
    },
    computed: {
      ...mapGetters(['filters']),
    },
    watch: {
      form: {
        deep: true,
        handler(newVal) {
          console.log({newVal})

          this.setFilters(newVal).then((res) => {
            this.getFilteredProducts(res)
          })
        }
      }
    },
    mounted() {
    },
    methods: {
      ...mapActions(['setFilters', 'getFilteredProducts']),
      addLanguage(language) {
        this.form.languages.push(language)
      },

      removeLanguage(language) {
        this.form.languages = this.form.languages.filter(lang => lang != language)
      }

    },
  };
</script>


<style lang="scss">
  @import 'colors';

  .section--babysitter-custom-filters {
    .h4 {
      margin-top: 1rem;
    }

    .custom-checkbox {
      width: 100%;
    }

    input:checked + label {
      color: $primary
    }

    .custom-control-label {
      cursor:     pointer;
      flex-grow: 1;

      &:hover {
        color: $primary;
      }
    }
  }
</style>
