
<template lang="pug">
    b-dropdown.is-small-dropdown(text="Choose")
      b-dropdown-item(v-for="(lang, i) in availableLanguages", :key="i", @click="select(lang)", :id="'lang_'+lang")
        i.flag-icon(:class="'flag-icon-'+lang.substring(3).toLowerCase()")
</template>

<script>
import { nodeApi } from "@/config";
export default {
  name: "LanguageSelector",
  props: [
    "language",
    "reload",
    "showChoose"
  ],
  data() {
    return {
      availableLanguages: [
        "en-US",
        "en-GB",
        "fr-FR",
        "hu-HU",
        "zh-CN",
        "zh-TW",
        "es-ES",
        "it-IT",
        "pt-BR",
        "ru-RU"
      ]
    };
  },
  created() {},
  methods: {
    select(lang) {
      if (this.reload) {
        window.location.reload()
      } else {
        this.$emit("select", lang);
      }
    }
  }
};
</script>

