<template lang="pug">
  b-button(
    :variant="(filteredProducts.length && filtersKey != filtersKeyApplied) ? 'primary' : 'outline-primary'"
    :disabled="!filteredProducts.length || (filtersKeyApplied && filtersKeyApplied == filtersKey)"
    v-shortkey="['enter']"
    @shortkey="applyFilters()"
    @click="applyFilters()"
  )
    | {{ text || 'APPLY FILTERS' }}
    span.ml-1( v-if="filteredProducts.length" )
      | (
      transition(name="slide-up" mode="out-in")
        span(
          :key="filteredProducts.length"
        )
          | {{filteredProducts.length}}
      | )
</template>

<script>
  import {mapGetters, mapActions} from "vuex";

  export default {
    name: "ButtonApplyFilters",
    props: [
      'text'
    ],
    computed: {
      ...mapGetters(['filteredProducts', 'filtersKey', 'filtersKeyApplied']),
    },
    methods: mapActions(['applyFilters'])
  };
</script>
